<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ cartItems.length | pluralizeProducts }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText v-model="formData.name" type="text" :error="formError.name" title="ФИО"
                          placeholder="Введите ваше полное имя"/>
            <BaseFormText v-model="formData.address" type="text" :error="formError.address" title="Адрес доставки"
                          placeholder="Введите ваш адрес"/>
            <BaseFormText v-model="formData.phone" type="tel" :error="formError.phone" title="Телефон"
                          placeholder="Введите ваш телефон"/>
            <BaseFormText v-model="formData.email" type="email" :error="formError.email" title="Email"
                          placeholder="Введите ваш Email"/>
            <BaseFormTextarea v-model="formData.comment" :error="formError.comment" title="Комментарий к закау"
                              placeholder="Ваши пожелания"/>
          </div>

<!--          <div class="cart__options">-->
<!--            <h3 class="cart__title">Доставка</h3>-->
<!--            <ul class="cart__options options">-->
<!--              <li class="options__item">-->
<!--                <label class="options__label">-->
<!--                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">-->
<!--                  <span class="options__value">-->
<!--                    Самовывоз <b>бесплатно</b>-->
<!--                  </span>-->
<!--                </label>-->
<!--              </li>-->
<!--              <li class="options__item">-->
<!--                <label class="options__label">-->
<!--                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">-->
<!--                  <span class="options__value">-->
<!--                    Курьером <b>500 ₽</b>-->
<!--                  </span>-->
<!--                </label>-->
<!--              </li>-->
<!--            </ul>-->

<!--            <h3 class="cart__title">Оплата</h3>-->
<!--            <ul class="cart__options options">-->
<!--              <li class="options__item">-->
<!--                <label class="options__label">-->
<!--                  <input class="options__radio sr-only" type="radio" name="pay" value="card">-->
<!--                  <span class="options__value">-->
<!--                    Картой при получении-->
<!--                  </span>-->
<!--                </label>-->
<!--              </li>-->
<!--              <li class="options__item">-->
<!--                <label class="options__label">-->
<!--                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">-->
<!--                  <span class="options__value">-->
<!--                    Наличными при получении-->
<!--                  </span>-->
<!--                </label>-->
<!--              </li>-->
<!--            </ul>-->
<!--          </div>-->
        </div>

        <div class="cart__block">
          <span v-if="cartItems.length === 0">Корзина пуста</span>
          <OrderList :items="cartItems"/>

          <div class="cart__total">
<!--            <p>Доставка: <b>500 ₽</b></p>-->
            <p>Итого: <b>{{ cartCountItems | pluralizeProducts }}</b> на сумму <b>{{ cartTotal | numberFormat }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit" :disabled="submitLoading">
            Оформить заказ
          </button>

          <BasePreloader v-if="submitLoading" />
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from "@/components/BaseFormText";
import BaseFormTextarea from "@/components/BaseFormTextarea";
import BasePreloader from "@/components/BasePreloader";
import OrderList from "@/components/OrderList";
import {API_BASE_URL, API_REQUEST_DELAY} from "@/config";
import axios from "axios";
import {mapGetters} from "vuex";
import numberFormat from "@/helpers/numberFormat";
import pluralizeProducts from "@/helpers/pluralize";

export default {
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      submitLoading: false,
    }
  },
  filters: {
    numberFormat,
    pluralizeProducts
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.submitLoading = true;

      return (new Promise(resolve => setTimeout(resolve, API_REQUEST_DELAY)))
          .then(() => {
            axios.post(API_BASE_URL + '/api/orders', {
              ...this.formData
            }, {
              params: {
                userAccessKey: this.$store.state.userAccessKey
              }
            })
                .then(response => {
                  this.$store.commit('resetCart');
                  this.$store.commit('updateOrderInfo', response.data);
                  this.$router.push({name: 'orderInfo', params: {id: response.data.id}});
                  this.submitLoading = false;
                })
                .catch(error => {
                  this.formError = error.response.data.error.request || {};
                  this.formErrorMessage = error.response.data.error.message;
                })
          })
    }
  },
  components: {BaseFormText, BaseFormTextarea, BasePreloader, OrderList},
  computed: {
    ...mapGetters({cartItems: 'cartDetailProducts', cartTotal: 'cartTotalPrice', cartCountItems: 'cartCountItems'})
  }
}
</script>