export default function pluralizeProducts(n) {
    let idx;

    if (n % 10 === 1 && n % 100 !== 11) {
        idx = 'товар';
    } else if ([2, 3, 4].includes(n % 10) && ![12, 13, 14].includes(n % 100)) {
        idx = 'товара';
    } else {
        idx = 'товаров';
    }
    return `${n} ${idx}`;
}
