<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': currentPage === 1}"
         href="#" aria-label="Предыдущая страница" @click.prevent="prevPage">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
      <a href="javascript:void(0)" class="pagination__link" :class="{'pagination__link--current': pageNumber === page}"
         @click="paginate(pageNumber)">
        {{ pageNumber }}
      </a>
    </li>
    <li class="pagination__item">
      <a class="pagination__link pagination__link--arrow" :class="{'pagination__link--disabled': currentPage === pages}"
         href="#" aria-label="Следующая страница" @click.prevent="nextPage">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage'],
  data() {
    return {
      currentPage: this.page
    }
  },
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    }
  },
  watch: {
    page(value) {
      this.currentPage = value;
    }
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page);
    },
    prevPage() {
      if (this.currentPage !== 1) {
        this.paginate(this.currentPage - 1);
      }
    },
    nextPage() {
      if (this.currentPage < this.pages) {
        this.paginate(this.currentPage + 1);
      }
    }
  }
}
</script>

